import HButton from '../../../components/HButton.vue'
import { pathToBase64 } from 'image-tools'
import { uploadFace } from '../../../api/config.js'
import { mapMutations } from 'vuex';
import { getFaceUrl } from '../../../api/index.js'

export default{
	components:{
		HButton
	},
	data(){
		return{
			hasPhoto:false,
			faceUrl:undefined
		}
	},
	onLoad(option) {
		let faceUrl = uni.getStorageSync('userInfo').faceUrl;
		if (faceUrl) {
		  this.getFaceUrl(faceUrl);
		} else {
		  this.hasPhoto= false
		}
	},
	methods:{
		...mapMutations(['MessageBox']),
		getFaceUrl(faceUrl){
			getFaceUrl({faceUrl}).then(res=>{
				this.faceUrl = res.data
				this.hasPhoto = truck
			})
		},
		takePicture(){
			let _this = this
			let userInfo = uni.getStorageSync('userInfo')
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['camera'], //从相册选择
			    success: function (res) {
					console.log(res)
					let tempFilePath = res.tempFilePaths[0]
					// 适用 h5 app wx小程序
					uni.showLoading({
						title: '上传中',
					})
					pathToBase64(tempFilePath)
					  .then(base64 => {
					    // console.log(base64)
						let data ={
							hasPhoto:_this.hasPhoto,
							userId:userInfo.id,
							tempFilePath:tempFilePath,
							faceBase64:base64
						}
						uploadFace(data).then(res=>{
							uni.hideLoading()
							if(res.code==1){
								let faceUrl = res.data;
								let userInfo = uni.getStorageSync('userInfo')
								userInfo.faceUrl = faceUrl;
								uni.setStorageSync('userInfo', userInfo);
								_this.setData({
									hasPhoto: true,
									faceUrl: tempFilePath
								})
							}else{
								uni.showToast({
								  title: '照片无法识别，请重新拍摄',
								  icon:"none",
								  duration:2000
								})
							}
						})
					  })
					  .catch(error => {
						uni.hideLoading()
					    console.error('图片转base64失败',error)
					  }) 
			    }
			});
		},
		showTips(){
			this.MessageBox({
				title:'人脸录入须知',
				type: 'HTML',
				msg:'人脸照片拍摄及上传，仅用于设备的人脸识别开门方式，不会用作其他商业用途。',
				visible: true
			})
		}
		
	}
}
